@import "../../assets/scss/app";

.footer {
  @include container;
  padding-top: 40px;
  padding-bottom: 40px;

  @include desktop {
    padding-top: 77px;
    padding-bottom: 33px;
  }

  .inner {
    @include inner;

    .topBlock {
      display: flex;
      flex-direction: column;
      padding-bottom: 60px;

      @include desktop {
        flex-direction: row;
        padding-bottom: 50px;
      }

      .leftPart {

        .logo {
          width: 236px;
          height: 175px;
          display: block;
          margin-left: -25px;
          margin-top: -25px;
          margin-bottom: -60px;

        }

        .stay {
          font-weight: 600;
          font-size: 20px;
          line-height: 150%;
        }

        .dont {
          margin-top: 5px;
          font-weight: 400;
          font-size: 16px;
          line-height: 150%;
        }

        .form {
          margin-top: 20px;
          display: flex;
          align-items: center;
          height: 54px;
          background: #FFFFFF;
          border-radius: 10px;
          padding-left: 16px;
          padding-right: 3px;
          box-sizing: border-box;

          @include desktop {
            width: 413px;
            padding-left: 20px;
            padding-right: 4px;
          }

          .field {
            border: none;
            outline: none;
            flex-grow: 1;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;

            &::placeholder {
              font-weight: 400;
              font-size: 12px;
              line-height: 150%;
              color: #777777;
              opacity: 0.5;
            }

          }

          .submit {
            @include button;
            @include centerRow;
            background: #A363F4;
            border-radius: 10px;
            width: 327px;
            height: 46px;
            font-weight: 700;
            font-size: 16px;
            line-height: 150%;
            transition: 0.3s;

            @include desktop {
              width: 160px;
            }

            &:hover {
              background: #7146a9;
            }

          }


        }

        .policy {
          margin-top: 16px;

          @include desktop {
            margin-top: 10px;
          }
        }


      }

      .right {
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #D3D3D3;
        margin-top: 60px;

        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-row-gap: 60px;

        @include desktop {
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(1, 1fr);
          grid-row-gap: 0;
          margin-top: 0;
          flex-grow: 1;
          margin-left: 170px;
        }

        .group {

          .groupLabel {
            font-weight: 600;
            font-size: 18px;
            line-height: 150%;
            color: #959595;
          }

          .groupLinks {
            margin-top: 16px;

            .link {
              display: block;
              text-decoration: none;
              font-weight: 400;
              font-size: 16px;
              line-height: 150%;

              &:not(:first-child) {
                margin-top: 14px;
              }
            }
          }
        }
      }

    }

    .bottomBlock {
      border-top: 1px solid #546371;
      padding-top: 32px;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include desktop {
        padding-top: 24px;
        height: 72px;
        flex-direction: row;
      }

      .socialLinks {
        .socialLink {
          &:not(:first-child) {
            margin-left: 18px;

            @include desktop {
              margin-left: 14px;
            }
          }
        }
      }

      .bottom {
        margin-top: 20px;
        align-self: stretch;

        @include desktop {
          margin-top: 0;
          flex-grow: 1;
          margin-left: 220px;
          display: flex;
          align-items: center;
          justify-content: space-between;
        }

        .mark {
          @include onlyDesktop();
          font-weight: 400;
          font-size: 14px;
          line-height: 150%;
        }

        .bottomLinks {
          align-self: stretch;
          display: flex;
          align-items: center;
          justify-content: space-evenly;

          @include desktop {
            justify-content: flex-start;
          }

          .bottomLink {
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            text-decoration-line: underline;
            color: #D3D3D3;

            @include desktop {
              &:not(:first-child) {
                margin-left: 34px;
              }
            }
          }

        }


      }
    }
  }

}
