@import "../../assets/scss/app";

.team {
  @include container;
  padding-top: 136px;

  @include desktop {
    padding-top: 93px;
  }

  .inner {
    @include inner;


    .title {
      @include h2;
    }

    .items {
      margin-top: 52px;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include desktop {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 24px;
      }

      .item {
        box-sizing: border-box;
        width: 306px;
        height: 293px;
        padding-top: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 1px solid #8D4BE6;
        border-radius: 10px;

        &:not(:first-child) {
          margin-top: 32px;

          @include desktop {
            margin-top: 0;
          }

        }

        img {
          display: block;
          width: 120px;
        }

        .name {
          font-weight: 600;
          font-size: 18px;
          line-height: 140%;
          text-align: center;
          letter-spacing: 0.01em;
          margin-top: 10px;

          @include desktop {
            margin-top: 13px;
          }
        }

        .job {
          font-weight: 400;
          font-size: 14px;
          line-height: 140%;
          text-align: center;
          letter-spacing: 0.01em;
          margin-top: 11px;

          @include desktop {
            margin-top: 8px;
          }

        }

        .links {
          @include centerRow;
          margin-top: 17px;

          a {
            @include centerRow;
            width: 35px;
            height: 35px;
            background: #413E61;
            border-radius: 50%;
            text-decoration: none;
            transition: 0.3s;

            &:not(:first-child) {
              margin-left: 16px;
            }

            &:hover {
              background: #69659d;
            }
          }

        }
      }

    }
  }
}
