@import "../../assets/scss/app";

.roadmap {
  //overflow: hidden;
  @include container;
  padding-top: 67px;

  @include desktop {
    padding-top: 80px;
  }

  .inner {
    @include inner;

    .title {
      @include h2;

      @include desktop {
        width: 613px;
      }
    }

    .stepsMobile {
      @include onlyMobile(flex);
      flex-direction: column;
      margin-top: 16px;

      .step {
        &:nth-child(even) {
          align-self: flex-end;
        }

        .imgWrapper {
          position: relative;
          height: 200px;
          width: 200px;
          //border: 2px solid red;

          img {
            position: absolute;
            left: calc(65%);
            top: 65%;
            transform: translate(-50%, -50%);
          }
        }

        &:nth-child(2) .imgWrapper img {
          top: calc(50%);
          left: calc(55%);
        }

        &:nth-child(3) .imgWrapper img {
          left: calc(77%);
        }

        &:nth-child(4) .imgWrapper img {
          top: calc(50%);
          left: calc(55%);
        }

        &:nth-child(5) .imgWrapper img {
          top: calc(70%);
          left: calc(80%);
        }

        .options {
          width: 240px;

          .optionItem {
            display: flex;
            align-items: center;

            &:not(:first-child) {
              margin-top: 12px;
            }

            .value {
              margin-left: 15px;
              font-weight: 400;
              font-size: 14px;
              line-height: 130%;
            }
          }
        }
      }


    }

    .stepsDesktop {
      @include onlyDesktop();

      .img {
        margin: 70px auto 0;
      }

      .steps {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        margin-left: 35px;
        margin-top: -60px;

        .step {

          &:nth-child(1) {
            margin-top: -115px;
          }

          &:nth-child(3) {
            margin-top: -110px;
            margin-left: 15px;
          }

          &:nth-child(4) {
            margin-top: 0px;
            margin-left: 35px;
          }

          &:nth-child(5) {
            margin-top: -110px;
            margin-left: 65px;
          }

          .options {
            .optionItem {
              display: flex;
              align-items: center;

              &:not(:first-child) {
                margin-top: 12px;
              }

              .value {
                margin-left: 15px;
                font-weight: 400;
                font-size: 14px;
                line-height: 130%;
              }
            }
          }
        }
      }
    }


  }

}
