@import "../../assets/scss/app";

.products {
  @include container;
  padding-top: 70px;

  @include desktop {
    padding-top: 84px;
  }

  .inner {
    @include inner;

    @include desktop {
      width: 1000px;
    }


    .title {
      @include h2;
      @include desktop {
        width: 647px;
      }
    }

    .text {
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      margin-top: 25px;

      @include desktop {
        margin-top: 21px;
        width: 665px;
        margin-left: auto;
        margin-right: auto;
      }

    }

    .items {
      margin-top: 32px;

      .item {
        margin-top: 64px;

        @include desktop {
          display: flex;
        }

        &:nth-child(even) {
          @include desktop {
            flex-direction: row-reverse;
          }
        }

        .imgWrapper {
          position: relative;
          height: 350px;

          @include desktop {
            width: 50%;
          }

          img {
            position: absolute;
            height: 100%;
            @include absoluteCenter;
          }
        }

        .texts {
          margin-top: 24px;

          @include desktop {
            width: 50%;
            margin-top: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
          }

          .subtitle {
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
          }

          .description {
            color: #A4A3C7;
            margin-top: 8px;
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;

            @include desktop {
              margin-top: 12px;
              font-size: 20px;
              line-height: 140%;
            }
          }
        }
      }
    }
  }
}
