@import "../../assets/scss/app";

.header {
  @include container;
  //overflow: hidden;
  padding-top: 32px;

  @include desktop {
    padding-top: 26px;
  }

  .inner {
    @include inner;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      height: 40px;
      width: 200px;
      //border: 1px solid red;
      position: relative;

      img {
        @include absoluteCenter;
        width: 257px;
        height: 191px;

        @include desktop {
          width: 236px;
          height: 175px;
        }
      }
    }

    .links {
      @include onlyDesktop(flex);
      @include absoluteCenter;

      .link {
        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
        color: #C6C6E9;
        transition: 0.3s;
        text-decoration: none;
        position: relative;

        &::after {
          content: "";
          display: block;
          position: absolute;
          left: 0px;
          bottom: 0px;
          width: 0%;
          height: 1px;
          background: #A363F4;
          transition: 0.3s;
        }

        &:hover {
          color: #A363F4;

          &::after {
            width: 100%;
          }
        }

        &:not(:first-child) {
          margin-left: 42px;
        }
      }

    }

    .popupBtnWrapper {
      position: relative;
      z-index: 1;

      .popupBtn {
        @include button;
        width: 100px;
        height: 44px;
        background: #1F1F41;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 21px;
        padding-right: 17px;
        box-sizing: border-box;
        transition: 0.3s;

        &:hover {
          background: #323269;
        }
      }

      .popup {
        position: absolute;
        top: 50px;
        right: 0;
        z-index: 999;
        width: 283px;
        height: 220px;
        background: #171b26;
        padding: 32px 28px;
        box-sizing: border-box;

        .btn {
          @include button;
          width: 228px;
          height: 56px;
          background: #A363F4;
          border-radius: 10px;
          @include centerRow;
          font-weight: 700;
          font-size: 16px;
          line-height: 150%;
          text-align: center;
          transition: 0.3s;

          &:hover {
            background: #7347ab;
          }
        }

        .items {
          margin-top: 28px;

          .item {
            display: flex;
            align-items: center;

            &:not(:first-child) {
              margin-top: 10px;
            }

            p {
              margin-left: 10px;
              font-weight: 400;
              font-size: 16px;
              line-height: 150%;
            }
          }
        }


      }


    }




  }
}
