@import "../../assets/scss/app";

.table {
  //@include centerRow;
  overflow: auto;
  margin-top: 48px;

  //scrollbar-width: none;
  //&::-webkit-scrollbar {
  //  display: none;
  //
  //  @include desktop {
  //    display: block;
  //  }
  //}

  @include desktop {
    margin-top: 80px;
    @include centerRow;
  }

  .inner {
    width: calc(1266px + 48px);

    @include desktop {
      width: calc(1521px + 100px);
    }

    .card {
      background: #0F121C;
      border-radius: 30px;
      box-sizing: border-box;
      padding: 50px 80px;
      width: 1266px;
      margin-left: 24px;
      margin-right: 24px;
      height: 704px;

      @include desktop {
        padding: 50px 95px;
        width: 1521px;
        height: 776px;
        margin-left: 50px;
        margin-right: 50px;
      }

      .head {
        padding: 0 24px 24px;
        display: grid;
        grid-template-columns: repeat(7, 1fr);

        .headCell {
          font-weight: 700;
          font-size: 20px;
          line-height: 140%;
          text-transform: uppercase;

          @include desktop {
            font-size: 22px;
          }
        }
      }

      .body {
        .row {
          border-top: 1px solid #142254;
          padding: 0 24px;
          height: 50px;
          display: grid;
          grid-template-columns: repeat(7, 1fr);

          &:last-child {
            border-bottom: 1px solid #142254;
          }

          @include desktop {
            height: 50px;
          }

          .cell {
            display: flex;
            align-items: center;

            p {
              font-weight: 500;
              font-size: 14px;
              line-height: 140%;

              @include desktop {
                font-size: 16px;
              }
            }
          }

        }
      }

      .footer {
        padding: 24px 24px 0;
        display: grid;
        grid-template-columns: repeat(7, 1fr);

        .footerCell {
          font-weight: 700;
          font-size: 18px;
          line-height: 140%;
          text-transform: uppercase;

          @include desktop {
            font-size: 22px;
          }
        }
      }


    }

  }
}
