@import "../../assets/scss/app";

.aboutUs {
  overflow: hidden;

  @include container;
  padding-top: 65px;

  @include desktop {
    padding-top: 0;
  }

  .inner {
    @include inner;
    display: flex;
    flex-direction: column;

    @include desktop {
      flex-direction: row;
      align-items: center;
    }

    .top {
      display: flex;
      flex-direction: column;

      .title {
        @include h1;
      }

      .text {
        margin-top: 16px;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;

        @include desktop {
          margin-top: 24px;
          font-size: 24px;
          line-height: 140%;
        }
      }

      img {
        align-self: center;
        width: 505.92px;

        @include desktop {
          display: none;
        }
      }

      .buttons {
        align-self: center;
        margin-top: 8px;
        display: flex;
        flex-direction: column;

        @include desktop {
          align-self: flex-start;
          margin-top: 36px;
          flex-direction: row;
        }

        .btn {
          @include button;
          @include centerRow;
          width: 274px;
          height: 74px;
          border: 1px solid #6F739D;
          border-radius: 60px;
          transition: 0.3s;

          p {
            margin-left: 12px;
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;

          }

          &:nth-child(1) {
            background: #1E2437;

            &:hover {
              border-color: #4b4e6b;
              background: #10141d;
            }
          }

          &:nth-child(2) {
            margin-top: 12px;

            @include desktop {
              margin-top: 0;
              margin-left: 32px;
            }

            &:hover {
              border-color: #4b4e6b;
            }
          }

        }
      }
    }

    .bottom {
      display: none;


      @include desktop {
        display: block;
        width: 550px;
        transition: 0.3;
      }
    }

  }

}
